<template>
  <div class="nk-app-root" v-if="isUserLoggedIn">
    <div class="nk-main">
      <app-sidebar v-if="isUserLoggedIn"></app-sidebar>
      <div class="nk-wrap" :class="{ 'nk-wrap-nosidebar': !isUserLoggedIn }">
        <app-navbar v-if="isUserLoggedIn"></app-navbar>
        <div class="nk-content">
          <div class="container-fluid mb-4" v-if="!empty(flashMessage)">
            <div class="nk-content-inner">
              <div class="nk-content-body">
                <div class="nk-block">
                  <div :class="`alert alert-fill alert-${flashMessage.type}`">
                    <strong>{{ flashMessage.message }}</strong>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <router-view />
        </div>
        <app-footer v-if="isUserLoggedIn"></app-footer>
      </div>
    </div>

    <loader
      v-if="$store.state.isLoading && $store.state.requestFlag === null"
    ></loader>

    <file-upload
      :input-id="'global-upload'"
      :extentions="['png', 'jpg', 'jpeg', 'gif', 'webp']"
      @after-uploading="reloadPage"
      :post-data="$store.state.entityImagePost"
    ></file-upload>
    <bootstrap-alert
      :modalTitle="$store.state.deleteAlert.title"
      :modalContent="$store.state.deleteAlert.content"
      :actionButton="'Yes, Delete'"
      @accept-alert="deletePaginationRecord"
      @close="$store.state.deleteAlert.modalOpen = false"
      :actionButtonClasses="'btn btn-danger'"
      v-if="$store.state.deleteAlert.modalOpen"
    ></bootstrap-alert>
    <spinner v-if="$store.state.requestFlag === 'PAGINATION_SEARCH'"></spinner>
  </div>
  <router-view v-else />
</template>
<script>
import NavBar from "./Shared/NavBar.vue";
import SideBar from "./Shared/SideBar.vue";
import Footer from "./Shared/Footer.vue";
import Loader from "@/components/Loader.vue";
import Spinner from "@/components/Spinner.vue";
export default {
  name: "app",
  components: {
    "app-navbar": NavBar,
    "app-sidebar": SideBar,
    "app-footer": Footer,
    Loader,
    Spinner,
  },
  computed: {
    flashMessage() {
      return this.$store.state.flashMessage;
    },
     appSettings() {
      return this.$store.state.settings;
    },
  },
  methods: {},
  beforeMount() {
    this.getSettings();
    if (this.isUserLoggedIn) {
      this.$http.get("auth/me").then((response) => {
        this.$store.dispatch("setCurrentUser", { ...response.data.user });
      });
    }
  },
  watch: {
    $route(to) {
      if (to.name == "login") {
        document.body.classList.add("pg-auth");
        document.body.classList.remove("has-sidebar");
      } else {
        document.body.classList.remove("pg-auth");
        document.body.classList.add("has-sidebar");
      }
      if (this.flashMessage.message) {
        setTimeout(() => {
          this.$store.commit("SET_FLASH_MESSAGE", {});
        }, 5000);
      }
    },
  },
  mounted() {
    document.addEventListener("click", (evt) => {
      const flyoutElements = [
        ...document.querySelectorAll("div.dropdown-menu"),
      ];
      if (flyoutElements.length) {
        let targetElement = evt.target; // clicked element
        var clickedOutSide = true;
        do {
          if (flyoutElements.includes(targetElement)) {
            // This is a click inside. Do nothing, just return.
            clickedOutSide = false;
          }
          // Go up the DOM
          targetElement = targetElement.parentNode;
        } while (targetElement);
        if (clickedOutSide && 1 > 2) {
          [...document.querySelectorAll("div.dropdown-menu")].forEach((elt) => {
            elt.classList.remove("show");
          });
        }
      }
    });
  },
};
</script>
